import React, {useState, useEffect}  from 'react';
import style from './CodeDescription.module.css';


//    :
const CodeDescription = ({arboniaModel, height, type, quantitySections, valve, connectionType, connectionSize, airCooler, airCoolerLocation, airCoolerSize, pressure, totalLenght, totalPower, drain}) => {

	const [bracket, setBracket] = useState('  : ZB0233');
	useEffect(() => {

		if(height.value > 295 && height.value < 495) {
			setBracket('  : ZB0233');
		} else if (height.value > 495 && height.value < 695) {
			setBracket('  : ZB0235');
		} else if (height.value > 695 && height.value < 3001) {
			setBracket(' : ZB0237');
		} else {
			setBracket(' :  ');
		}
	}, [height]);



	let name = ` Arbonia ${arboniaModel}${valve.model === 'RRV' ? 'V' : ''} ${quantitySections}/${valve.code}/${connectionType.code}/${connectionSize.code}/${airCooler.code}/${airCoolerLocation.code}/${airCoolerSize.code}/${drain.code} AF`;


	let description = `${name}
${type.code}-, ${valve.description},  - ${type.value} ,  - ${height.value} .   - ${quantitySections},   - ${totalLenght} ,  ${connectionType.description}   ${connectionSize.description}, ${airCoolerLocation.description},  : ${pressure.description}.     T50 = ${totalPower} . :  (RAL 9016).   ${bracket}.`;

	return <div className={style.container}>
		<p> :</p>
		<div className={style.description}>
			<textarea name="codeDescription" cols="65" rows="7" value={description}/>
		</div>
	</div>
};

export default CodeDescription;