import React from 'react';
import style from './ArboniaRRN.module.css';
import {Dropdown} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import SizesImage from '../SizesImageBlock/SizesImage'

//    :
const ArboniaRRN = (props) => {
	const drain = React.createRef();

	const onChangeDrain = () => {
		let d;
		let price;
		if (!drain.current.checked) {
			d = drain.current.value;
			price = 0;
		} else {
			d = 4;
			price = 100;
		}
		props.setDrain(d, price);
	};
	const {image} = props.connectionTypeOptions;

	return <div className={style.container}>

		<h3>  (RRN):</h3>

		<div className={style.main}>

				<div className={style.connection_block}>
					<p> [6]  :</p>
					<div className={style.dropdown}>
						<Dropdown
								onChange={props.setConnectionTypesData}
								placeholder='  : '
								fluid={true}
								selection
								item
								options={props.extraOptionRRNDisplay}
								upward={false}
						/>
					</div>

					{props.connectionTypeOptions.image &&
					<div className={style.connection_wrapper}>
						<div className={style.connection_img}>
							<img src={image.src} alt=" "/>
						</div>

						<div className={style.connection_size_block}>
							<p>[7]  :</p>
							{props.connectionSizeOptions.map(el => {
								return <div>
									<input onChange={(e) => {props.setExtraOptions(e)}}
												 data-price={el.price}
												 defaultChecked={el.defaultChecked}
												 data-description={el.description}
												 name={el.name} type="radio"
												 value={el.value}/> - {el.text}
								</div>
							})}
						</div>
					</div>}
				</div>

			<div className={style.air_location_wrapper}>
				{props.connectionTypeOptions.image &&
				<div className={style.air_location_block}>
					<p>[9]  :</p>
					<div className={style.cooler_img}>
						<img src={props.airCoolerLocation.image}
								 alt={props.airCoolerLocation.description}/>
					</div>
					<div>
						{props.airCoolerLocation.description}
					</div>
				</div>}
			</div>

			{props.sizesImage &&
			<div className={style.size_image_wrapper}>
				<SizesImage />
			</div>}
		</div>


	{/*   =>  */}
		<div className={style.additional}>
			<div className={style.aircooler_block}>
				<p>[8]  :</p>
				<div>
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} data-price="5.12"
								 name="airooler" type="radio" value="1" defaultChecked/>    
					;
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} data-price="0"
								 name="airooler" type="radio" value="4"/> ;
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} data-price="0"
								 name="airooler" type="radio" value="3"/>   (!  );
				</div>
			</div>

			<div className={style.air_connection_block}>
				<p>[10]   :</p>
				<div>
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} name="airoolerSize" type="radio" value="38"/>  G 3/8";
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} name="airoolerSize" type="radio" value="12" defaultChecked/>  G 1/2";
				</div>
			</div>

			<div className={style.drain_block}>
				<p>[11] :</p>
				<input onChange={onChangeDrain} name="drain" ref={drain} type="checkbox" defaultChecked={false} value='3/0/0'/>
				
				();
			</div>

			{/*<p>[12]   :</p>*/}
			{/*<p>[13]    :</p>*/}

			<div className={style.pressure_block}>
				<p>[14]   :</p>
				<div>
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} data-price="0"
								 data-description="10 "
								 name="pressure" type="radio" value="10" defaultChecked/> : 10  (1000 );
					<input onChange={(e) => {
						props.setExtraOptions(e)
					}} data-price="300"
								 data-description="16  (. )"
								 name="pressure" type="radio" value="16"/>   : 16  (1600 );
				</div>
			</div>
		</div>

	</div>
};

export default ArboniaRRN;



