import React from 'react';
import style from './Specifications.module.css';
import image from '../../../images/types/typeSize2-2.png'
import TotalPriceContainer from "../../Configurator/TotalPrice/TotalPriceContainer";


const Specifications = (props) => {
	return (
			<div className={style.container}>

				<div className={style.wrapper}>
					<h3> </h3>
					<div className={style.specification}>
						<div className={style.item}>
							<span> : </span>
							<div className={style.item_option}>
								{props.specifications.modelCode
										? props.specifications.modelCode
										: " .... "}
							</div>
						</div>

						<div className={style.item}>
							<span>  [H]: </span>
							<div className={style.item_option}>
								{props.height.value} 
							</div>
						</div>

						<div className={style.item}>
							<span>  [T]: </span>
							<div className={style.item_option}>
								{props.type.value} 
							</div>
						</div>

						<div className={style.item}>
							<span>   [T1]: </span>
							<div className={style.item_option}>
								{props.type.value / 2} 
							</div>
						</div>

						<div className={style.item}>
							<span>  [L]: </span>
							<div className={style.item_option}>
								{props.quantitySections > 0 ? props.totalLenght : 0} 
							</div>
						</div>

						<div className={style.item}>
							<span>  [GL]: </span>
							<div className={style.item_option}>
								{props.quantitySections} .
							</div>
						</div>

						<div className={style.item_pw}>
							<span>  (<b>T50</b>/ T60/ T70): </span>
							<div className={style.item_option_pw}>
								<b>{props.totalPower}</b>/ {(props.totalPower/0.8).toFixed(0)}/ {(props.totalPower/0.65).toFixed(0)} 
							</div>
						</div>
					</div>
				</div>

				<div className={style.image_wrapper}>

					{props.type.image && props.specifications.modelCode
							? <img src={props.type.image} alt="size image"/>
							: null
					}

					{props.showPrice &&
					<div className={style.price}>
						<TotalPriceContainer/>
					</div>}

				</div>

			</div>
	);
}

export default Specifications;